body {
  font-family: 'Segoe UI', sans-serif;
  background: #f0f4f8;
  color: #1f2937;
  margin: 0;
  padding: 0;
}

.quiz-container {
  background: white;
  border-radius: 20px;
  padding: 32px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.08);
  max-width: 850px;
  margin: 40px auto;
}

.question-title {
  font-size: 18px;
  font-weight: 600;
}
